/* navbar------------------------------------------------------------- */ 


.navbar {
    position: fixed;
    top: 0;
    left: 0;
    
    width: 100%;
    padding: 0.75rem 0.75rem;
    background:hsla(33, 70%, 37%, 1);
    background:var(--brown);
    border-radius: 0px;
    z-index: 1;
  }
 

  .nav-header {
    
    display: -webkit-flex;
    
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    height: 6rem;
    transition:ease-in 0.2s;
  }

  .nav-shrink {
    height: 5vh;
    transition:ease-in 1s;
  }
  .nav-btn {
    font-size: 36px;
    background: transparent;
    border: none;
    cursor: pointer;
    outline: none;
  }
  .nav-icon {
    font-size: 1.5rem;
    color: black;
  }
  .nav-links {
    height: 0;
    overflow: hidden;
    transition:ease-in;
    list-style-type: none;
  }
  .nav-links a {
    display: block;
    text-decoration: none;
    padding: 1rem 0;
    color: black;
    transition:ease-out;
    text-align: center;
    font-size: 1rem;
    font-weight: 600;
    letter-spacing:1px;
  }
  .nav-links a:hover {
    color: blueviolet;
  }
  
  .show-nav {
    height: 100px;
  }

  /* end -navbar------------------------------------------------------------- */
  
  
   
   

/* cart ---------------------------------------------------------------------- */
.xcircle-btn{
  font-size: 55px; 
  background: transparent;
    border: none;
    cursor: pointer;
    outline: none;
    z-index: 10;
    
}
.cart-item{
  z-index: 9}
.cartunit{
  z-index: 10

}
.cart-total {
  z-index: 10
}


  .cart-btn  {
    float:inline-end;
    font-size: 40px; 
    background: transparent;
    border: none;
    cursor: pointer;
    outline: none;
  }

.cover{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  display: grid;
  place-items: center;
  /* transition:ease-in 1s   ; */
  visibility: hidden;
  z-index: -1;
}

.sshowCart {
  visibility: visible;
  z-index:3;
  
}
.cart-container {

  background: hsla(9, 63%, 18%, 1);

  background: var(--black);
  border-radius: 15px;
  width: 50vw;
  height: 50vh;
  max-width: 50vw;
  text-align: center;
  display: grid;
  grid-column: 1fr 1fr 1fr;
  place-items: center;
  position: relative;
}


 
  

  /* resposivenes -----*/
  @media screen and (min-width: 768px) {
    .nav-btn {
      display: none;
    }
    .nav-center {
      max-width: 1170px;
      margin: 0 auto;
      display: -webkit-flex;
      display: flex;
    }
    .nav-links {
      height: auto;
      display: -webkit-flex;
      display: flex;
      margin-left: 7rem;
      -webkit-justify-content: space-between;
              justify-content: space-between;
    }
    .nav-links a {
      margin: 0 1rem;
      padding: 0.5rem 0;
    }
  }
  

